


















import { Component, Vue, Watch } from 'vue-property-decorator'

/**
 * @title  
 * @desc 
 */
@Component({
  components: {
  },
  })
export default class TextAdjustExample extends Vue {
  size: string = "md"

  index: number = 0

  list = [
    {
      color: 'orange',
      text: 'U'
    },
    {
      color: 'purple',
      text: 'Lucy'
    },
    {
      color: 'Tom',
      text: 'yellow'
    },
    {
      color: 'info',
      text: 'Spencer'
    },
    {
      color: 'error',
      text: ''
    }
  ]

  change () {
    let len = this.list.length
    if (this.index < len - 1) this.index++
    else this.index = 0
  }
}
