module.exports = "<template>\n<div>\n  <v-radio-group v-model=\"size\">\n    <v-radio-button label=\"lg\"></v-radio-button>\n    <v-radio-button label=\"md\"></v-radio-button>\n    <v-radio-button label=\"sm\"></v-radio-button>\n  </v-radio-group>\n\n  <v-radio-group v-model=\"border\" class=\"mx-2\">\n    <v-radio-button label=\"0\"></v-radio-button>\n    <v-radio-button label=\"1\"></v-radio-button>\n  </v-radio-group>\n\n  <v-alert class=\"my-2\" :description=\"` ${count} `\"></v-alert>\n\n   <div class=\"m-3\">\n     <v-table row-key=\"key\" pageable :bordered=\"bordered\" :size=\"size\" :data-source=\"dataSource\" @selection-change=\"selectionChange\">\n       <v-table-column type=\"selection\" :selections=\"selections\"></v-table-column>\n       <v-table-column type=\"index\" label=\"#\"></v-table-column>\n       <v-table-column prop=\"name\" label=\"\"></v-table-column>\n       <v-table-column prop=\"age\" label=\"\"></v-table-column>\n       <v-table-column prop=\"address\" label=\"\"></v-table-column>\n       <v-table-column column-key=\"opt\" label=\"\">\n         <template slot-scope=\"{}\">\n           <v-button type=\"text\"></v-button> | <v-button type=\"text\"></v-button>\n         </template>\n       </v-table-column>\n     </v-table>\n   </div>\n</div>\n</template>\n<script lang=\"ts\">\nimport { Component, Vue, Watch } from 'vue-property-decorator'\nimport { TableColumnSelectionCbParam } from '../../../../../../src/components/type'\n\n/**\n * @title  \n * @desc  Checkbox, row-key\n */\n@Component({\n  components: {\n  },\n  })\nexport default class SelectionExample extends Vue {\n  dataSource = [ { key: 1, name: '', age: 85, address: '  ' },\n  { key: 2, name: '', age: 42, address: '  ' },\n  { key: 3, name: '', age: 92, address: '  ' },\n  { key: 4, name: '', age: 97, address: '  ' },\n  { key: 5, name: '', age: 95, address: '  ' },\n  { key: 6, name: '', age: 86, address: ' ' },\n  { key: 7, name: '', age: 29, address: '  ' },\n  { key: 8, name: '', age: 55, address: '  ' },\n  { key: 9, name: '', age: 31, address: '  ' },\n  { key: 10, name: '', age: 92, address: '  ' },\n  { key: 11, name: '', age: 42, address: '  ' } ]\n\n  size: string = 'lg'\n\n  border: string = '0'\n\n  get bordered () {\n    return this.border !== '0'\n  }\n\n  count: number = 0\n\n  selections = [\n    {\n      label: '',\n      key: 'all',\n      onSelect: ({allData, selectionKeySet}: TableColumnSelectionCbParam) => {\n        allData.forEach(v => {\n          selectionKeySet.add(v.key)\n        })\n      }\n    },\n    {\n      label: '',\n      key: 'odd',\n      onSelect: ({currentData, selectionKeySet}: TableColumnSelectionCbParam) => {\n        currentData.forEach((v, i) => {\n          if (i % 2 === 0) {\n            selectionKeySet.add(v.key)\n          } else {\n            selectionKeySet.delete(v.key)\n          }\n        })\n      }\n    },\n    {\n      label: '',\n      key: 'clear',\n      onSelect: ({selectionKeySet}: TableColumnSelectionCbParam) => {\n        selectionKeySet.clear()\n      }\n    }\n  ]\n\n  selectionChange (keys: any[]) {\n    this.count = keys.length\n  }\n}\n</script>\n"