































import { Component, Vue, Watch } from 'vue-property-decorator'
import { TableColumnSelectionCbParam } from '../../../../../../src/components/type'

/**
 * @title  
 * @desc  Checkbox, row-key
 */
@Component({
  components: {
  },
  })
export default class SelectionExample extends Vue {
  dataSource = [ { key: 1, name: '', age: 85, address: '  ' },
  { key: 2, name: '', age: 42, address: '  ' },
  { key: 3, name: '', age: 92, address: '  ' },
  { key: 4, name: '', age: 97, address: '  ' },
  { key: 5, name: '', age: 95, address: '  ' },
  { key: 6, name: '', age: 86, address: ' ' },
  { key: 7, name: '', age: 29, address: '  ' },
  { key: 8, name: '', age: 55, address: '  ' },
  { key: 9, name: '', age: 31, address: '  ' },
  { key: 10, name: '', age: 92, address: '  ' },
  { key: 11, name: '', age: 42, address: '  ' } ]

  size: string = 'lg'

  border: string = '0'

  get bordered () {
    return this.border !== '0'
  }

  count: number = 0

  selections = [
    {
      label: '',
      key: 'all',
      onSelect: ({allData, selectionKeySet}: TableColumnSelectionCbParam) => {
        allData.forEach(v => {
          selectionKeySet.add(v.key)
        })
      }
    },
    {
      label: '',
      key: 'odd',
      onSelect: ({currentData, selectionKeySet}: TableColumnSelectionCbParam) => {
        currentData.forEach((v, i) => {
          if (i % 2 === 0) {
            selectionKeySet.add(v.key)
          } else {
            selectionKeySet.delete(v.key)
          }
        })
      }
    },
    {
      label: '',
      key: 'clear',
      onSelect: ({selectionKeySet}: TableColumnSelectionCbParam) => {
        selectionKeySet.clear()
      }
    }
  ]

  selectionChange (keys: any[]) {
    this.count = keys.length
  }
}
