module.exports = "<template>\n<div>\n  <div class=\"my-3\">\n    <v-form :model=\"form\" ref=\"form\">\n      <v-form-item label=\"\" prop=\"username\" required\n        :rules=\"[{validator: 'required', message: '', trigger: 'blur'}]\">\n        <v-input v-model=\"form.username\" placeholder=\"\"></v-input>\n      </v-form-item>\n      <v-form-item :label=\"'' + i\" :prop=\"'domains.' + i +'.value'\" v-for=\"(domain, i) in form.domains\"\n      :key=\"i\" required :rules=\"[{validator: 'required', message: '', trigger: 'blur'}]\">\n        <v-input v-model=\"domain.value\" placeholder=\"\"></v-input>\n        <v-button class=\"ml-2\" @click=\"del(i)\"></v-button>\n      </v-form-item>\n      <div class=\"pl-5\">\n        <v-button class=\"mr-2\" @click=\"add\"></v-button>\n        <v-button class=\"mr-2\" @click=\"reset\"></v-button>\n        <v-button color=\"primary\" @click=\"submit\"></v-button>\n      </div>\n    </v-form>\n  </div>\n</div>\n</template>\n<script lang=\"ts\">\nimport { Component, Vue, Watch } from 'vue-property-decorator'\nimport { VForm } from 'src'\n\n/**\n * @title   \n * @desc \n */\n@Component({\n  components: {\n  },\n  })\nexport default class DynamicExample extends Vue {\n  form = {\n    username: 'Alison',\n    domains: [\n      {\n        value: ''\n      }\n    ]\n  }\n\n  add () {\n    this.form.domains.push({ value: '' })\n  }\n\n  del (i: number) {\n    this.form.domains.splice(i, 1)\n  }\n\n  submit () {\n    this.$refs.form.validate().then(({ valid, errors }) => {\n      if (valid) {\n        this.$vua.$message.success('')\n      } else {\n        this.$vua.$message.error('')\n        console.log('errors: ', errors)\n      }\n    })\n  }\n\n  reset () {\n    this.$refs.form.resetFields()\n  }\n\n  $refs!: {\n    form: VForm\n  }\n}\n</script>\n"