




















import { Component, Vue, Watch } from 'vue-property-decorator'
import { VForm, VTree, Node, TreeNodeFilterFn } from '@/index'

let index = 1000

/**
 * @title 
 * @desc 
 */
@Component({
  components: {
  },
  })
export default class IconContentExample extends Vue {
  dataSource = [{
    key: '1',
    label: ' 1',
    disabled: true,
    children: [{
      key: '1-1',
      label: ' 1-1',
      children: [{
        key: '1-1-1',
        label: ' 1-1-1'
      }]
    }]
  }, {
    key: '2',
    label: ' 2',
    children: [{
      key: '2-1',
      label: ' 2-1',
      children: [{
        key: '2-1-1',
        label: ' 2-1-1'
      }]
    }, {
      key: '2-2',
      label: ' 2-2',
      children: [{
        key: '2-2-1',
        label: ' 2-2-1'
      }]
    }]
  }, {
    key: '3',
    label: ' 3',
    children: [{
      disabled: true,
      key: '3-1',
      label: ' 3-1',
      children: [{
        key: '3-1-1',
        label: ' 3-1-1'
      }]
    }, {
      key: '3-2',
      label: ' 3-2',
      children: [{
        key: '3-2-1',
        label: ' 3-2-1'
      }]
    }]
  }]

  append (node: Node) {
    if (!node.data.children) {
      this.$set(node.data, 'children', [])
    }
    node.data.children.push({
      key: index++,
      label: 'label' + index
    })
    node.expandNode(true)
  }

  del (node: Node) {
    if (node.parent) {
      let children = node.parent.data.children || node.parent.data
      let idx = children.findIndex((v: any) => node.data.key === v.key)
      if (idx >= 0) children.splice(idx, 1)
    }
  }

  $refs!: {
    tree: VTree
  }
}
