




































































import { Component, Vue, Watch } from 'vue-property-decorator'
import { VForm } from 'src'

/**
 * @title   
 * @desc 
 */
@Component({
  components: {
  },
  })
export default class ValidExample extends Vue {
  form = {
    username: 'Alison',
    password: '',
    sex: 'M',
    skill: ['Js'],
    city: 'shanghai',
    birthday: '1992/08/29',
    tel: ['', ''],
    profession: []
  }

  rules = {
    username: [
      {validator: 'required', message: '', trigger: 'blur'}
    ],
    password: [
      {validator: 'required'},
      {validator: 'length', min: 6, max: 8},
    ],
    skill: [
      {validator: 'required'},
      {validator: 'length', min: 1},
    ],
    city: [
       {validator: 'required'},
    ],
    birthday: [
       {validator: 'required'},
    ],
    tel: [
       {validator: (rule: any, value: any) => {
         return new Promise((resolve, reject) => {
           setTimeout(() => {
             if (value && value[0] && value[1]) {
               return resolve()
             }
             return reject(new Error(''))
           }, 2000)
         })
       }},
    ],
    profession: [
      {validator: 'length', min: 1, message: ''},
    ]
  }

  labelPosition: string = 'right'

  layout: string = 'vertical'

  professions = [
    {
      key: '1',
      label: '',
      children: [
        {
          key: '1-1',
          label: '',
        },
        {
          key: '1-2',
          label: '',
        }
      ]
    },
    {
      key: '2',
      label: '',
      children: [
        {
          key: '2-1',
          label: '',
        },
        {
          key: '2-2',
          label: '',
        }
      ]
    }
  ]

  submit () {
    this.$refs.form.validate().then(({ valid, errors }) => {
      if (valid) {
        this.$vua.$message.success('')
      } else {
        this.$vua.$message.error('')
        console.log('errors: ', errors)
      }
    })
  }

  reset () {
    this.$refs.form.resetFields()
  }

  $refs!: {
    form: VForm
  }
}
