module.exports = "<template>\n<div>\n   <div class=\"m-3\">\n     <p> ()</p>\n     <v-alert class=\"my-2\" :description=\"value || '>'\"></v-alert>\n     <v-date-picker :disabled-date=\"disabledDate\" v-model=\"value\" placeholder=\"\" clearable></v-date-picker>\n   </div>\n\n   <div class=\"m-3\">\n     <p> (,  18)</p>\n     <v-alert class=\"my-2\" :description=\"value5 || '>'\"></v-alert>\n     <v-date-picker show-time :disabled-date=\"disabledDate\" :disabled-time=\"disabledTime\" v-model=\"value5\" placeholder=\"\" clearable></v-date-picker>\n   </div>\n\n   <div class=\"m-3\">\n     <p> ()</p>\n     <v-alert class=\"my-2\" :description=\"value1 || '>'\"></v-alert>\n     <v-month-picker :disabled-date=\"disabledDate1\" v-model=\"value1\" placeholder=\"\" clearable></v-month-picker>\n   </div>\n\n   <div class=\"m-3\">\n     <p> (5)</p>\n     <v-alert class=\"my-2\" :description=\"value2 || '>'\"></v-alert>\n     <v-year-picker :disabled-date=\"disabledDate2\" v-model=\"value2\" placeholder=\"\" clearable></v-year-picker>\n   </div>\n\n    <div class=\"m-3\">\n      <p> ()</p>\n     <v-alert class=\"my-2\" :description=\"value3 || '>'\"></v-alert>\n     <v-week-picker :disabled-date=\"disabledDate3\" v-model=\"value3\" placeholder=\"\" clearable></v-week-picker>\n   </div>\n\n   <div class=\"m-3\">\n      <p> ()</p>\n     <v-alert class=\"my-2\" :description=\"'' + value4 || '>'\"></v-alert>\n     <v-range-picker :disabled-date=\"disabledDate4\" v-model=\"value4\" start-placeholder=\"\" end-placeholder=\"\" clearable></v-range-picker>\n   </div>\n</div>\n</template>\n<script lang=\"ts\">\nimport { Component, Vue, Watch } from 'vue-property-decorator'\n\n/**\n * @title  \n * @desc  disabledDate  disabledTime  disabledTime  showTime \n */\n@Component({\n  components: {\n  },\n  })\nexport default class DisabledDateExample extends Vue {\n  value: string = '2018/12/11'\n\n  value1: string = '2018/12'\n\n  value2: string = '2018'\n\n  value3: string = '2018/45'\n\n  value4: string[] = ['2018/12/10', '2019/02/25']\n\n  value5: string = ''\n\n  disabledDate (date: Date) {\n    let now = new Date()\n    return now.getFullYear() !== date.getFullYear() || now.getMonth() !== date.getMonth()\n  }\n\n  disabledTime (hour: number, sec: number, min: number, date: Date) {\n    if (!date) return true\n    let dayOfWeek = date.getDay()\n    if (dayOfWeek >= 1 && dayOfWeek <= 5) {\n      if (hour * 3600 + min * 60 + sec < (18 * 3600)) return true\n    }\n    return false\n  }\n\n  disabledDate1 (date: Date) {\n    let now = new Date()\n    return now.getFullYear() !== date.getFullYear() || date.getMonth() > 5\n  }\n\n  disabledDate2 (date: Date) {\n    let now = new Date()\n    return Math.abs(date.getFullYear() - now.getFullYear()) > 4\n  }\n\n  disabledDate3 (date: Date) {\n    return date.getMonth() % 2 === 0\n  }\n\n  disabledDate4 (date: Date) {\n    let now = new Date()\n    return now.getFullYear() !== date.getFullYear() || Math.abs(date.getMonth() - now.getMonth()) > 2\n  }\n}\n</script>\n"