module.exports = "<template>\n<div>\n   <div class=\"m-3\">\n     <p>9:00 - 5:00</p>\n     <v-time-picker v-model=\"value\" placeholder=\"\" clearable :disabled-time=\"disabledTime\"></v-time-picker>\n   </div>\n</div>\n</template>\n<script lang=\"ts\">\nimport { Component, Vue, Watch } from 'vue-property-decorator'\n\n/**\n * @title \n * @desc  disabledHourdisabledMindisabledSec \n */\n@Component({\n  components: {\n  },\n  })\nexport default class DisabledItemExample extends Vue {\n  value: string = ''\n\n  disabledTime (hour: number, min: number, sec: number) {\n    if (hour < 9) return true\n    if (hour > 17) return true\n    if (hour === 17 && (min > 0 || sec > 0)) return true\n    return false\n  }\n}\n</script>\n"