module.exports = "<template>\n<div>\n  <v-radio-group v-model=\"size\">\n    <v-radio-button label=\"lg\"></v-radio-button>\n    <v-radio-button label=\"md\"></v-radio-button>\n    <v-radio-button label=\"sm\"></v-radio-button>\n  </v-radio-group>\n\n   <v-radio-group v-model=\"border\" class=\"mx-2\">\n    <v-radio-button label=\"0\"></v-radio-button>\n    <v-radio-button label=\"1\"></v-radio-button>\n  </v-radio-group>\n\n  <v-radio-group class=\"mx-2\" v-model=\"overflow\">\n    <v-radio-button label=\"wrap\"></v-radio-button>\n    <v-radio-button label=\"nowrap\"></v-radio-button>\n    <v-radio-button label=\"ellipsis\"></v-radio-button>\n  </v-radio-group>\n\n  <v-alert class=\"my-2\" :description=\"` ${count} `\"></v-alert>\n\n   <div class=\"m-3\">\n     <v-table pageable remote :size=\"size\" :bordered=\"bordered\"\n      row-key=\"key\"\n      :loading=\"loading\"\n      :data-source=\"remoteData.data\"\n      v-bind=\"remoteData\"\n      @selection-change=\"selectionChange\"\n      @remote-change=\"onRemoteChange\">\n       <v-table-column type=\"selection\" :selections=\"selections\"></v-table-column>\n       <v-table-column prop=\"name\" label=\"\"></v-table-column>\n       <v-table-column prop=\"age\" label=\"\" sortable :filters=\"filters\"></v-table-column>\n       <v-table-column prop=\"address\" :overflow=\"overflow\" label=\"\"></v-table-column>\n       <v-table-column prop=\"remark\" :overflow=\"overflow\" label=\"\" width=\"120px\"></v-table-column>\n       <v-table-column column-key=\"opt\" label=\"\">\n         <template slot-scope=\"{}\">\n           <v-button type=\"text\"></v-button> | <v-button type=\"text\"></v-button>\n         </template>\n       </v-table-column>\n     </v-table>\n   </div>\n</div>\n</template>\n<script lang=\"ts\">\nimport { Component, Vue, Watch } from 'vue-property-decorator'\nimport { TableColumnSelectionCbParam, DataRemoteChangeParam, DataRemoteChangeType } from '@/index'\n\n/**\n * @title \n * @desc   \n */\n@Component({\n  components: {\n  },\n  })\nexport default class RemoteExample extends Vue {\n  remoteData = {\n    dataSource: [],\n    pagination: {\n      currentPage: 1,\n      pageSize: 10,\n      total: 0\n    },\n    sorter: null,\n    filters: []\n  }\n\n  count: number = 0\n\n  loading: boolean = false\n\n  dataSource = [ { key: 1,\n    name: '',\n    age: 58,\n    address: '  ',\n    remark: '' },\n  { key: 2,\n    name: '',\n    age: 86,\n    address: '  ',\n    remark: '' },\n  { key: 3,\n    name: '',\n    age: 55,\n    address: '  ',\n    remark: '' },\n  { key: 4,\n    name: '',\n    age: 53,\n    address: '  ',\n    remark: '' },\n  { key: 5,\n    name: '',\n    age: 84,\n    address: '  ',\n    remark: '' },\n  { key: 6,\n    name: '',\n    age: 91,\n    address: '  ',\n    remark: '' },\n  { key: 7,\n    name: '',\n    age: 25,\n    address: '  ',\n    remark: '' },\n  { key: 8,\n    name: '',\n    age: 22,\n    address: '  ',\n    remark: '' },\n  { key: 9,\n    name: '',\n    age: 67,\n    address: '  ',\n    remark: '' },\n  { key: 10,\n    name: '',\n    age: 92,\n    address: '  ',\n    remark: '' },\n  { key: 11,\n    name: '',\n    age: 82,\n    address: '  ',\n    remark: '' },\n  { key: 12,\n    name: '',\n    age: 94,\n    address: '  ',\n    remark: '' },\n  { key: 13,\n    name: '',\n    age: 91,\n    address: '  -',\n    remark: '' },\n  { key: 14,\n    name: '',\n    age: 42,\n    address: '  ',\n    remark: '' } ]\n\n  size: string = 'lg'\n\n  border: string = '0'\n\n  get bordered () {\n    return this.border !== '0'\n  }\n\n  overflow=\"wrap\"\n\n  filters = [\n    {\n      text: '22',\n      value: 22\n    },\n    {\n      text: '25',\n      value: 25\n    },\n    {\n      text: '42',\n      value: 42\n    },\n     {\n      text: '58',\n      value: 58\n    }\n  ]\n\n  selections = [\n    {\n      label: '',\n      key: 'all',\n      onSelect: ({allData, selectionKeySet}: TableColumnSelectionCbParam) => {\n        allData.forEach(v => {\n          selectionKeySet.add(v.key)\n        })\n      }\n    },\n    {\n      label: '',\n      key: 'odd',\n      onSelect: ({currentData, selectionKeySet}: TableColumnSelectionCbParam) => {\n        currentData.forEach((v, i) => {\n          if (i % 2 === 0) {\n            selectionKeySet.add(v.key)\n          } else {\n            selectionKeySet.delete(v.key)\n          }\n        })\n      }\n    },\n    {\n      label: '',\n      key: 'clear',\n      onSelect: ({selectionKeySet}: TableColumnSelectionCbParam) => {\n        selectionKeySet.clear()\n      }\n    }\n  ]\n\n  selectionChange (keys: any[]) {\n    this.count = keys.length\n  }\n\n  onRemoteChange (param: DataRemoteChangeParam, type: DataRemoteChangeType) {\n    console.log(type)\n    this.mockQuery(param)\n  }\n\n  mockQuery ({ pagination, sorter, filters }: DataRemoteChangeParam) {\n    this.loading = true\n    setTimeout(() => {\n      let all = this.dataSource.slice(0)\n      if (filters && filters.length > 0) {\n        all = all.filter((v: any) => {\n          return filters.every(filter => {\n            if (!filter.values || filter.values.length < 1) return true\n            return filter.values.includes(v[filter.prop])\n          })\n        })\n      }\n      if (sorter && sorter.prop && sorter.order) {\n        all.sort((a: any, b: any) => {\n          return (a[sorter.prop] - b[sorter.prop]) * (sorter.order === 'desc' ? -1 : 1)\n        })\n      }\n      let maxPage = Math.ceil(all.length / pagination.pageSize)\n      let currentPage = maxPage < pagination.currentPage ? maxPage : pagination.currentPage\n      let start = (currentPage - 1) * pagination.pageSize\n      let end = start + pagination.pageSize\n      Object.assign(this.remoteData, {\n        dataSource: all.slice(start, end),\n        pagination: {\n          currentPage: currentPage,\n          pageSize: pagination.pageSize,\n          total: all.length\n        },\n        sorter: sorter,\n        filters: filters\n      })\n      this.loading = false\n    }, 1500)\n  }\n\n  mounted () {\n    this.mockQuery(this.remoteData)\n  }\n}\n</script>\n"