  size: string = 'lg'

  border: string = '1'

  get bordered () {
    return this.border !== '0'
  }

  overflow="wrap"

  spanFn ({ rowIndex, columnIndex, column }: any) {
    let prop = column.prop
    if (rowIndex === -1) { // represent header
      if (prop === 'code') {
        return {
          rowspan: 1,
          colspan: 2
        }
      } else if (prop === 'tel') {
        return {
          rowspan: 1,
          colspan: 0
        }
      }
    } else {
      if (prop === 'code') {
        if (rowIndex % 2 === 0) {
          return {
            rowspan: 2,
            colspan: 1
          }
        } else {
          return {
            rowspan: 0,
            colspan: 1
          }
        }
      }
    }
  }
}
