module.exports = "<template>\n<div>\n  <v-radio-group v-model=\"size\">\n    <v-radio-button label=\"lg\"></v-radio-button>\n    <v-radio-button label=\"md\"></v-radio-button>\n    <v-radio-button label=\"sm\"></v-radio-button>\n  </v-radio-group>\n\n  <v-radio-group v-model=\"border\" class=\"mx-2\">\n    <v-radio-button label=\"0\"></v-radio-button>\n    <v-radio-button label=\"1\"></v-radio-button>\n\n  <v-radio-group class=\"mx-2\" v-model=\"overflow\">\n    <v-radio-button label=\"wrap\"></v-radio-button>\n    <v-radio-button label=\"nowrap\"></v-radio-button>\n    <v-radio-button label=\"ellipsis\"></v-radio-button>\n  </v-radio-group>\n\n  </v-radio-group>\n\n   <div class=\"m-3\">\n     <v-table pageable :bordered=\"bordered\" :size=\"size\" :data-source=\"dataSource\" :span-fn=\"spanFn\">\n       <v-table-column type=\"index\" label=\"#\" fixed=\"left\"></v-table-column>\n       <v-table-column prop=\"name\" :overflow=\"overflow\" label=\"\"></v-table-column>\n       <v-table-column prop=\"age\" :overflow=\"overflow\" label=\"\"></v-table-column>\n       <v-table-column prop=\"code\" :overflow=\"overflow\" label=\"\"></v-table-column>\n       <v-table-column prop=\"tel\" :overflow=\"overflow\" label=\"\"></v-table-column>\n       <v-table-column prop=\"address\" :overflow=\"overflow\" label=\"\"></v-table-column>\n       <v-table-column column-key=\"opt\" label=\"\">\n         <template slot-scope=\"{}\">\n           <v-button type=\"text\"></v-button> | <v-button type=\"text\"></v-button>\n         </template>\n       </v-table-column>\n     </v-table>\n   </div>\n</div>\n</template>\n<script lang=\"ts\">\nimport { Component, Vue, Watch } from 'vue-property-decorator'\n\n/**\n * @title  /\n * @desc ,  rowIndex  -1\n */\n@Component({\n  components: {\n  },\n  })\nexport default class SpanExample extends Vue {\n  dataSource = [ { key: 1,\n    name: '',\n    age: 63,\n    code: 7085,\n    tel: 5751474,\n    address: '  ',\n    remark: '' },\n  { key: 2,\n    name: '',\n    age: 28,\n    code: 4293,\n    tel: 4586065,\n    address: '  ',\n    remark: '' },\n  { key: 3,\n    name: '',\n    age: 74,\n    code: 3479,\n    tel: 7317719,\n    address: '  ',\n    remark: '' },\n  { key: 4,\n    name: '',\n    age: 95,\n    code: 6268,\n    tel: 6970634,\n    address: '  ',\n    remark: '' },\n  { key: 5,\n    name: '',\n    age: 48,\n    code: 1553,\n    tel: 1201211,\n    address: '  ',\n    remark: '' },\n  { key: 6,\n    name: '',\n    age: 59,\n    code: 9043,\n    tel: 1661005,\n    address: '  ',\n    remark: '' },\n  { key: 7,\n    name: '',\n    age: 30,\n    code: 9628,\n    tel: 4306714,\n    address: '  ',\n    remark: '' },\n  { key: 8,\n    name: '',\n    age: 72,\n    code: 5888,\n    tel: 6985576,\n    address: '  ',\n    remark: '' } ]\n\n  size: string = 'lg'\n\n  border: string = '1'\n\n  get bordered () {\n    return this.border !== '0'\n  }\n\n  overflow=\"wrap\"\n\n  spanFn ({ rowIndex, columnIndex, column }: any) {\n    let prop = column.prop\n    if (rowIndex === -1) { // represent header\n      if (prop === 'code') {\n        return {\n          rowspan: 1,\n          colspan: 2\n        }\n      } else if (prop === 'tel') {\n        return {\n          rowspan: 1,\n          colspan: 0\n        }\n      }\n    } else {\n      if (prop === 'code') {\n        if (rowIndex % 2 === 0) {\n          return {\n            rowspan: 2,\n            colspan: 1\n          }\n        } else {\n          return {\n            rowspan: 0,\n            colspan: 1\n          }\n        }\n      }\n    }\n  }\n}\n</script>\n"